







































import Vue from "vue";
import firebase from "firebase";
import "firebase/firestore";

export default Vue.extend({
  name: "list",
  data: function() {
    return {
      search: "",
      postList: []
    };
  },
  mounted() {
    const users = [];
    const data = firebase
      .database()
      .ref("users/")
      .once("value")
      // @ts-ignore
      .then(data => this.postList.push(...data.val()));
  },
  methods: {
    showMore: function(post: any, list: any) {
      list.forEach((element: { showMore: boolean }) => {
        element.showMore = false;
      });
      post.showMore = !post.showMore;
    }
  },
  computed: {
    filteredList() {
      function compare(a: any, b: any) {
        if (a.score < b.score) return 1;
        if (a.score > b.score) return -1;
        return 0;
      }
      let sortpost = (this as any).postList.sort(compare);
      return (this as any).postList.filter((sortpost: any) => {
        return sortpost.name
          .toLowerCase()
          .includes((this as any).search.toLowerCase());
      });
    }
  }
});
